const findSolution = (f, min, max) => {

    let x, res;

    let count = 0;
    while ( max - min > 1) {
        x = Math.round(min + (max - min) / 2);
        res = f(x);
        if (Math.abs(res) < 0.00001)
            break;
        else if (res < 0)
            max = x;
        else 
            min = x;    

        count++;
        if (count > 30) break;
    }    

    return x;

}

export const calcTerm = (loanAmount, interestRate, monthlyPayment) => {

    const f = (x) => {
        return calcMonthlyPayment(loanAmount, interestRate, x) - monthlyPayment;
    }

    let x = findSolution(f, 1, 100 * 12, monthlyPayment);
    return x;
}

export const calcMonthlyPayment = (loanAmount, interestRate, loanTerm, roundPayment = false) => {
    const monthlyInterestRate = interestRate / (12 * 100);
    const monthlyPayment = loanAmount * monthlyInterestRate * Math.pow(1 + monthlyInterestRate, loanTerm) / (Math.pow(1 + monthlyInterestRate, loanTerm) - 1);
    if (roundPayment)
        return Math.round(monthlyPayment);
    else
        return Math.round(100 * monthlyPayment) / 100;
}

export const calcPayments = (loanAmount, interestRate1, monthlyPayment1, interestRate2, monthlyPayment2) => {
    
    let res = [];
    addToPayments(res, loanAmount, interestRate1, monthlyPayment1, '1');
    addToPayments(res, loanAmount, interestRate2, monthlyPayment2, '2');

    return res;
}

export const addToPayments = (res, loanAmount, interestRate, monthlyPayment, index) => {
    
    let remaining = loanAmount;
    let n = 0;
    while(remaining > 0) {

        if (res.length === n)
            res.push({});

        let payment = monthlyPayment;
        let interest = Math.round(100* (remaining * interestRate / (100 * 12))) / 100;
        let principal = payment - interest;
        remaining -= principal;

        if (remaining < 20) {
            principal += remaining;
            payment += remaining; 
            remaining = 0;
        }
        res[n][index] = {
            payment: Math.round(payment * 100), 
            interest: Math.round(interest * 100), 
            principal: Math.round(principal * 100), 
            remaining: Math.round(remaining * 100)
        };
        n++;
    }
}

export const calcTotals = (payments) => {
    let totals = {'1':{}, '2': {}};
    for (let i in payments){
        for (let key of Object.keys(payments[i])) {
            totals[key].payment = (totals[key].payment || 0) + payments[i][key].payment;
            totals[key].interest = (totals[key].interest || 0) + payments[i][key].interest;
            totals[key].principal = (totals[key].principal || 0) + payments[i][key].principal;
        }
    }

    return totals;
}


