import React, { Component } from 'react';
import { Button, Table } from 'react-bootstrap'
import Form from "react-jsonschema-form";

import { calcTerm, calcMonthlyPayment, calcPayments, calcTotals } from './utils'
import './App.css';

class App extends Component {

  constructor(props) {
    super(props);

    const properties = {
      loanAmount: {
        type: "number",
        title: "Сумма кредита:"
      },
      interestRate1: {
        type: "number",
        title: "Процент 1:"
      },
      loanTerm1: {
        type: "number",
        title: "Срок кредита 1, мес:"
      },
      monthlyPayment1: {
        type: "number",
        title: "Платеж 1:"
      },
      interestRate2: {
        type: "number",
        title: "Процент 2:"
      },
      loanTerm2: {
        type: "number",
        title: "Срок кредита 2, мес:"
      },
      monthlyPayment2: {
        type: "number",
        title: "Платеж 2:"
      },
      roundPayment: {
        type: "boolean",
        title: "Округлять платеж"
      },
      toCalc: {
        type: "string",
        title: "Рассчитать:",
        enum: ["payment", "term"],
        enumNames: ["Платеж", "Срок"]
      },
    };

    const dependencies = {
      toCalc: {
        oneOf: [
          {
            properties: {
              toCalc: { enum: ["payment"] }
            },
            required: ["loanTerm1", "loanTerm2"]
          },
          {
            properties: {
              toCalc: { enum: ["term"] }
            },
            required: ["monthlyPayment1", "monthlyPayment2"]
          }
        ]
      }
    }

    this.schema = {
      title: 'Ипотечный калькулятор',
      type: "object",
      required: [
        "loanAmount",
        "interestRate1",
        "interestRate2"
      ],
      properties,
      dependencies
    };

    this.uiSchema = {
      "toCalc": {
        "ui:widget": "radio",
        "ui:options": {
          inline: true
        }
      }
    };

    this.state = {
      loanAmount: 1000000,
      interestRate1: 10,
      loanTerm1: 36,
      interestRate2: 11,
      loanTerm2: 36,
      toCalc: "payment",
      roundPayment: false,
      payments: [],
      totals: {}
    }
  }

  handleChange = ({ formData }) => {
    this.setState(formData);
  }

  calculate = (schema) => {

    const { loanAmount, interestRate1, monthlyPayment1, loanTerm1, interestRate2, monthlyPayment2, loanTerm2, toCalc, roundPayment } = this.state;
    if (toCalc === "term") {
      let loanTerm1 = calcTerm(loanAmount, interestRate1, monthlyPayment1);
      let loanTerm2 = calcTerm(loanAmount, interestRate2, monthlyPayment2);
      let payments = calcPayments(loanAmount, interestRate1, monthlyPayment1, interestRate2, monthlyPayment2);
      let totals = calcTotals(payments);
      this.setState({
        loanTerm1,
        loanTerm2,
        payments,
        totals
      })

    } else if (toCalc === "payment") {

      let monthlyPayment1 = calcMonthlyPayment(loanAmount, interestRate1, loanTerm1, roundPayment);
      let monthlyPayment2 = calcMonthlyPayment(loanAmount, interestRate2, loanTerm2, roundPayment);
      let payments = calcPayments(loanAmount, interestRate1, monthlyPayment1, interestRate2, monthlyPayment2);
      let totals = calcTotals(payments);
      this.setState({
        monthlyPayment1,
        monthlyPayment2,
        payments,
        totals
      })

    }

  }

  render() {

    const {payments, totals} = this.state;

    return (
      <div className="container">
        <Form
          schema={this.schema}
          uiSchema={this.uiSchema}
          formData={this.state}
          /*
          FieldTemplate={CustomFieldTemplate}
          ObjectFieldTemplate={ObjectField}
          */
          onChange={this.handleChange}
          onSubmit={this.calculate}
        >
          <div>
            <Button type="submit">Рассчитать</Button>
          </div>
        </Form>
        <Table striped bordered condensed hover style={{ marginTop: "15px" }}>
          <thead>
            <tr>
              <th>#</th>
              <th>Сумма 1</th>
              <th>Сумма ОД 1</th>
              <th>Сумма процентов 1</th>
              <th>Остаток ОД 1</th>
              <th>Сумма 2</th>
              <th>Сумма ОД 2</th>
              <th>Сумма процентов 2</th>
              <th>Остаток ОД 2</th>
            </tr>
          </thead>
          <tbody>
            {payments.map((val, i) => (
              <tr key={i} >
                <td>{i + 1}</td>
                <td>{val['1'] && val['1'].payment/100}</td>
                <td>{val['1'] && val['1'].principal/100}</td>
                <td>{val['1'] && val['1'].interest/100}</td>
                <td>{val['1'] && val['1'].remaining/100}</td>
                <td>{val['2'] && val['2'].payment/100}</td>
                <td>{val['2'] && val['2'].principal/100}</td>
                <td>{val['2'] && val['2'].interest/100}</td>
                <td>{val['2'] && val['2'].remaining/100}</td>
              </tr>
            ))}
          </tbody>
          <tfoot>
            <tr style={{fontWeight: "bold"}}>
              <td></td>
              <td>{totals['1'] && totals['1'].payment/100}</td>
              <td>{totals['1'] && totals['1'].principal/100}</td>
              <td>{totals['1'] && totals['1'].interest/100}</td>
              <td></td>
              <td>{totals['2'] && totals['2'].payment/100}</td>
              <td>{totals['2'] && totals['2'].principal/100}</td>
              <td>{totals['2'] && totals['2'].interest/100}</td>
              <td></td>
            </tr>
          </tfoot>
        </Table>
        <div>
          Разница {totals['1'] && totals['2'] && (totals['2'].payment - totals['1'].payment) / 100 }
        </div>  
      </div>
    );
  }
}

export default App;



